import React, { useState, useEffect, FC } from 'react';
import { format } from 'date-fns';
import CategorySelector from './CategorySelector';
import BudgetInput from './BudgetInput';
import { Expense, Budget } from './MonthlyBudgetTypes';

const MonthlyBudget: FC = () => {
  const [userEmail, setUserEmail] = useState<string>(''); // User email input
  const [categories, setCategories] = useState<string[]>([]); // List of categories
  const [selectedCategory, setSelectedCategory] = useState<string>(''); // Selected category
  const [monthlyBudgets, setMonthlyBudgets] = useState<Budget[]>([]); // Monthly budgets
  const [expenses, setExpenses] = useState<Expense[]>([]); // List of expenses
  const [showBudgetForm, setShowBudgetForm] = useState<boolean>(false); // Toggle budget form visibility
  const [showAddCategory, setShowAddCategory] = useState<boolean>(false); // Toggle add category input
  const [newCategory, setNewCategory] = useState<string>(''); // New category name
  const [isLoading, setIsLoading] = useState<boolean>(false); // Loading state
  const [isLoggedIn, setIsLoggedIn] = useState<boolean>(false); // Track login state

  useEffect(() => {
    const storedEmail = localStorage.getItem('Email');
    if (storedEmail) {
      setUserEmail(storedEmail); // Set email from localStorage
      setIsLoggedIn(true); // Automatically log in if email is found
    }
  }, []);

  useEffect(() => {
    if (userEmail && isLoggedIn) {
      setIsLoading(true);

      // Fetch expenses for the given user
      fetch(`/api/expenses/${userEmail}`)
        .then((response) => response.json())
        .then((data: Expense[]) => {
          const categoryNames = [
            ...new Set(data.map((expense) => expense.CategoryName.toLowerCase())),
          ];
          setCategories(categoryNames);
          setExpenses(data);
        })
        .catch((error) => console.error('Error fetching expense categories:', error));

      // Fetch monthly budgets for the given user
      fetch(`/api/monthlyBudgets/${userEmail}`)
        .then((response) => response.json())
        .then((data: Budget[]) => setMonthlyBudgets(data))
        .catch((error) => console.error('Error fetching budgets:', error))
        .finally(() => setIsLoading(false));
    }
  }, [userEmail, isLoggedIn]);

  const handleLogin = () => {
    if (userEmail) {
      localStorage.setItem('Email', userEmail); // Store email in localStorage
      setIsLoggedIn(true); // Update login state
    }
  };

  const handleCategorySelect = (category: string) => {
    if (category === 'Other') {
      setShowAddCategory(true);
    } else {
      setSelectedCategory(category.toLowerCase());
      setShowAddCategory(false);
    }
  };

  const handleAddCategory = async () => {
    if (newCategory && !categories.includes(newCategory.toLowerCase())) {
      try {
        const response = await fetch('/api/categories', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ CategoryName: newCategory, Email: userEmail }),
        });

        if (!response.ok) {
          const errorMessage = await response.text();
          console.error('Error creating category:', errorMessage);
          return;
        }

        const addedCategory = await response.json();
        setCategories([...categories, addedCategory.CategoryName.toLowerCase()]);
        setSelectedCategory(addedCategory.CategoryName.toLowerCase());
        setShowAddCategory(false);
        setNewCategory('');
      } catch (error) {
        console.error('Error creating category:', error);
      }
    }
  };

  const handleBudgetSave = (amount: number) => {
    const categoryExists = monthlyBudgets.some(
      (budget) => budget.CategoryName.toLowerCase() === selectedCategory
    );
    if (categoryExists) {
      alert('Category already exists in your budgets.');
      return;
    }

    const data = {
      Email: userEmail,
      CategoryName: selectedCategory,
      BudgetAmount: parseFloat(amount.toString()),
      SpentAmount: 0,
      BudgetMonth: new Date().toISOString(),
    };

    fetch('/api/monthlyBudgets', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(data),
    })
      .then((response) => {
        if (!response.ok) {
          return response.json().then((error) => {
            throw new Error(error.error);
          });
        }
        return response.json();
      })
      .then((data) => {
        alert('Budget saved successfully!');
        setMonthlyBudgets((prevBudgets) => [...prevBudgets, data]);
        setShowBudgetForm(false);
      })
      .catch((error) => console.error('Error saving budget:', error));
  };

  const handleBudgetDelete = (budgetId: number) => {
    fetch(`/api/monthlyBudgets/${budgetId}`, {
      method: 'DELETE',
    })
      .then((response) => {
        if (response.ok) {
          setMonthlyBudgets((prevBudgets) =>
            prevBudgets.filter((budget) => budget.BudgetID !== budgetId)
          );
        } else {
          console.error('Error deleting budget');
        }
      })
      .catch((error) => console.error('Error deleting budget:', error));
  };

  const calculateSpentAmount = (categoryName: string) => {
    return expenses
      .filter((expense) => expense.CategoryName.toLowerCase() === categoryName.toLowerCase())
      .reduce((total, expense) => total + expense.Amount, 0);
  };

  const isOverBudget = (budget: Budget) => {
    const spentAmount = calculateSpentAmount(budget.CategoryName);
    return spentAmount > budget.BudgetAmount;
  };

  return (
    <div>
      <h2>Monthly Budget</h2>
      {!isLoggedIn ? (
        <>
          <input
            type="email"
            placeholder="Enter your email"
            value={userEmail}
            onChange={(e) => setUserEmail(e.target.value)}
          />
          <button onClick={handleLogin}>Login</button>
        </>
      ) : (
        <>
          <div>
            <h4>Logged in as: {userEmail}</h4>
            <button onClick={() => setShowBudgetForm(true)}>Create Monthly Budget</button>
          </div>
          {showBudgetForm && (
            <div>
              <CategorySelector
                categories={['Select Category', ...categories, 'Other']}
                onSelect={handleCategorySelect}
              />
              {selectedCategory && selectedCategory !== 'Select Category' && !showAddCategory && (
                <BudgetInput onSave={handleBudgetSave} />
              )}
              {showAddCategory && (
                <div>
                  <input
                    type="text"
                    placeholder="Enter new category"
                    value={newCategory}
                    onChange={(e) => setNewCategory(e.target.value)}
                  />
                  <button onClick={handleAddCategory}>Add Category</button>
                </div>
              )}
            </div>
          )}

          <h3>Your Monthly Budgets</h3>
          {isLoading ? (
            <p>Loading...</p>
          ) : (
            <ul>
              {monthlyBudgets.map((budget) => (
                <li key={budget.BudgetID}>
                  {format(new Date(budget.BudgetMonth), 'MMMM yyyy')} - {budget.CategoryName}:{' '}
                  {budget.BudgetAmount} (Spent: {calculateSpentAmount(budget.CategoryName)})
                  <button onClick={() => handleBudgetDelete(budget.BudgetID)}>Delete</button>
                  {isOverBudget(budget) && (
                    <p style={{ color: 'red' }}>Over budget!</p>
                  )}
                </li>
              ))}
            </ul>
          )}
        </>
      )}
    </div>
  );
};

export default MonthlyBudget;