import React, { useEffect, useState } from 'react';
import './Analytics.css';
import ExpensesByCategoryChart from './ExpensesByCategoryChart';
import ExpensesByMonthChart from './ExpensesByMonthChart';

// Define the structure of an expense
interface Expense {
  ExpenseID: number;
  CategoryID: number;
  CategoryName: string;
  Amount: number;
  Date: string; // Changed to string for compatibility with JSON data
  Description?: string;
}

// Define the structure of an income
interface Income {
  IncomeID: number;
  Amount: number;
  Date: string; // Changed to string for compatibility with JSON data
  Description?: string;
}

const Analytics: React.FC = () => {
  const [expenses, setExpenses] = useState<Expense[]>([]); // Store expenses
  const [incomes, setIncomes] = useState<Income[]>([]); // Store incomes
  const [userEmail, setUserEmail] = useState<string>(''); // Store the user's email
  const [selectedMonth, setSelectedMonth] = useState<string>(''); // Store the selected month
  const [selectedYear, setSelectedYear] = useState<string>(''); // Store the selected year

  // Fetch data when the component mounts
  useEffect(() => {
    // Check if the email is already stored in localStorage
    const storedEmail = localStorage.getItem('Email');
    if (storedEmail) {
      setUserEmail(storedEmail); // Set email from localStorage
    }
  }, []);

  // Fetch data when the user's email changes
  useEffect(() => {
    if (userEmail) {
      // Fetch expenses for the given user email
      fetch(`/api/expenses/${userEmail}`)
        .then((response) => response.json())
        .then((data: Expense[]) => setExpenses(data))
        .catch((error) => console.error('Error fetching expenses:', error));

      // Fetch incomes for the given user email
      fetch(`/api/incomes/${userEmail}`)
        .then((response) => response.json())
        .then((data: Income[]) => setIncomes(data))
        .catch((error) => console.error('Error fetching incomes:', error));
    }
  }, [userEmail]);

  // Calculate total expenses
  const totalExpenses = expenses.reduce((acc, expense) => acc + expense.Amount, 0);

  // Calculate total incomes
  const totalIncomes = incomes.reduce((acc, income) => acc + income.Amount, 0);

  // Calculate savings or deficit
  const savings = totalIncomes - totalExpenses;
  const savingsLabel =
    savings >= 0
      ? `Saving: ${savings.toFixed(2)}`
      : `Deficit: ${Math.abs(savings).toFixed(2)}`;

  // Filter expenses based on the selected month and year
  const filteredExpenses = expenses.filter((expense) => {
    const expenseDate = new Date(expense.Date);
    const expenseMonth = expenseDate.toLocaleString('en-US', { month: 'long' });
    const expenseYear = expenseDate.getFullYear().toString();

    const isMonthMatch = selectedMonth ? expenseMonth === selectedMonth : true;
    const isYearMatch = selectedYear ? expenseYear === selectedYear : true;

    return isMonthMatch && isYearMatch;
  });

  // Extract unique months from expenses
  const uniqueMonths = Array.from(
    new Set(
      expenses.map((expense) =>
        new Date(expense.Date).toLocaleString('en-US', { month: 'long' })
      )
    )
  );

  // Extract unique years from expenses
  const uniqueYears = Array.from(
    new Set(
      expenses.map((expense) => new Date(expense.Date).getFullYear().toString())
    )
  );

  return (
    <div className="analytics-container">
      <h1>Analytics</h1>

      {/* Display email at the top */}
      {userEmail && <h3>Logged in as: {userEmail}</h3>}

      {/* Summary of expenses and incomes */}
      <div className="summary-container">
        <h2>Expense and Income Summary</h2>
        <div className="summary-box">
          <p>Total Expenses: {totalExpenses.toFixed(2)}</p>
          <p>Total Incomes: {totalIncomes.toFixed(2)}</p>
          <p>{savingsLabel}</p>
        </div>
      </div>

      {/* Month and Year selection */}
      <div className="filter-container">
        <div className="month-selector">
          <label htmlFor="month">Select Month:</label>
          <select
            id="month"
            value={selectedMonth}
            onChange={(e) => setSelectedMonth(e.target.value)}
          >
            <option value="">All Months</option>
            {uniqueMonths.map((month) => (
              <option key={month} value={month}>
                {month}
              </option>
            ))}
          </select>
        </div>
        <div className="year-selector">
          <label htmlFor="year">Select Year:</label>
          <select
            id="year"
            value={selectedYear}
            onChange={(e) => setSelectedYear(e.target.value)}
          >
            <option value="">All Years</option>
            {uniqueYears.map((year) => (
              <option key={year} value={year}>
                {year}
              </option>
            ))}
          </select>
        </div>
      </div>

      {/* Charts for visualization */}
      <div className="charts-container">
        <div className="chart">
          <ExpensesByCategoryChart expensesByCategory={filteredExpenses} />
        </div>
        <div className="chart">
          <ExpensesByMonthChart monthlyExpenses={filteredExpenses} />
        </div>
      </div>
    </div>
  );
};

export default Analytics;